body {
  display: grid;
  min-height: 100vh;
  margin: 0;
  padding: 0;
  background-color: #ffffff;
  color: #000000;
  font-family: "PT Mono", "Courier New", "Courier", monospace;
  font-style: normal;
  font-weight: 400;
  font-size: 16px; }

a {
  text-decoration: none;
  color: #000000; }

img {
  max-width: 100%;
  height: auto; }

/* pt-mono-regular - latin_cyrillic */
@font-face {
  font-family: "PT Mono";
  font-style: normal;
  font-weight: 400;
  font-display: swap;
  src: local(""), url("../fonts/pt-mono-v8-latin_cyrillic-regular.woff2") format("woff2"), url("../fonts/pt-mono-v8-latin_cyrillic-regular.woff") format("woff"); }

/* roboto-500 - cyrillic */
@font-face {
  font-family: "Roboto";
  font-style: normal;
  font-weight: 500;
  font-display: swap;
  src: local(""), url("../fonts/roboto-v20-cyrillic-500.woff2") format("woff2"), url("../fonts/roboto-v20-cyrillic-500.woff") format("woff"); }

/* roboto-condensed-regular- cyrillic */
@font-face {
  font-family: "Roboto Condensed";
  font-style: normal;
  font-weight: 400;
  font-display: swap;
  src: local(""), url("../fonts/roboto-condensed-v19-cyrillic-regular.woff2") format("woff2"), url("../fonts/roboto-condensed-v19-cyrillic-regular.woff") format("woff"); }

/* roboto-condensed-700 - cyrillic */
@font-face {
  font-family: "Roboto Condensed";
  font-style: normal;
  font-weight: 700;
  font-display: swap;
  src: local(""), url("../fonts/roboto-condensed-v19-cyrillic-700.woff2") format("woff2"), url("../fonts/roboto-condensed-v19-cyrillic-700.woff") format("woff"); }

.container {
  max-width: 1420px;
  margin: 0 auto; }

@media (max-width: 767px) {
  .container {
    max-width: 767px; } }

@media (max-width: 320px) {
  .container {
    max-width: 320px; } }

.visually-hidden {
  position: absolute;
  width: 1px;
  height: 1px;
  margin: -1px;
  border: 0;
  padding: 0;
  clip: rect(0 0 0 0);
  overflow: hidden; }

.header-site {
  position: relative;
  width: 100%;
  background-color: #1B2D37;
  background-image: url("../img/photo-1.png");
  background-repeat: no-repeat;
  background-size: 100% auto;
  z-index: -2; }
  .header-site::before {
    position: absolute;
    content: "";
    right: 0;
    width: 1920px;
    height: 776px;
    background-image: url("../img/photo-2.png");
    background-repeat: no-repeat;
    background-size: 1920px auto;
    z-index: -1; }

.header-site-wrap {
  display: flex;
  flex-direction: column; }

.header-site-nav {
  display: flex;
  flex-direction: row;
  align-items: center;
  margin-top: 32px;
  margin-bottom: 229px;
  padding: 0 5px; }

.header-logo {
  margin-right: auto; }

.site-navigation-list {
  display: flex;
  flex-direction: row;
  align-items: baseline;
  margin: 0;
  padding: 0;
  list-style: none; }

.site-navigation-link {
  margin-left: 35px; }

.site-navigation-link:nth-child(2n) {
  margin-left: 95px; }

.site-navigation-link:nth-child(3n) {
  margin-left: 49px; }

.site-navigation-link a {
  color: #ffffff; }

.site-navigation-link svg {
  vertical-align: middle; }

.site-navigation-link span {
  padding-left: 5px; }

.site-navigation-link--mdl {
  padding: 14px 27px;
  font-family: Roboto Condensed;
  font-weight: bold;
  font-size: 14px;
  line-height: 16px;
  text-align: center;
  letter-spacing: 0.05em;
  text-transform: uppercase;
  border: 1px solid #ffffff;
  border-radius: 5px; }

.header-hero {
  display: flex;
  flex-direction: column; }

.header-hero-wrap h2 {
  margin: 0 0 25px;
  font-family: Roboto Condensed;
  font-weight: bold;
  font-size: 80px;
  line-height: 94px;
  letter-spacing: 0.07em;
  text-transform: uppercase;
  color: #ffffff; }

.header-hero-wrap p {
  position: relative;
  max-width: 710px;
  margin: 0 0 45px;
  padding-left: 96px;
  font-size: 22px;
  line-height: 34px;
  color: #ffffff; }
  .header-hero-wrap p::before {
    position: absolute;
    content: "";
    left: 0;
    top: 17px;
    width: 66px;
    height: 2px;
    background-color: #ffffff; }

.header-hero-wrap-btn {
  margin-left: 96px;
  margin-bottom: 276px;
  padding: 17px 45px 17px;
  background-color: #00B2FF;
  border: none;
  border-radius: 5px;
  font-family: Roboto Condensed;
  font-weight: bold;
  font-size: 16px;
  line-height: 19px;
  text-align: center;
  letter-spacing: 0.05em;
  text-transform: uppercase;
  color: #ffffff; }

.header-hero-wrap-btn span {
  display: none; }

.scroll-up-link {
  position: fixed;
  top: 730px;
  width: auto;
  height: 13px;
  cursor: pointer;
  color: #fff;
  line-height: 16px;
  opacity: .5;
  transform: translate(-45px, 323%) rotate(-90deg); }

.advantages {
  position: relative;
  max-width: 1174px;
  margin-top: 111px;
  margin-bottom: 105px; }
  .advantages::before {
    position: absolute;
    content: "";
    top: -637px;
    left: -374px;
    width: 1920px;
    height: 1354px;
    background-image: url("../img/polygon.png");
